import Vue from 'vue'
import Router from 'vue-router'

import Left from '../views/layout/Left'
import Layout from '../views/layout/Layout'

Vue.use(Router)

export default new Router({
  routes: [
   
    {
      path: '/login',
      name: '登陆',
      component: ()=>import('@/views/login/login')
    },
    {
      path: '/content',
      name: '内容管理',
      component: Layout,
      children:[
        {
          path:'/',
          name:'hello',
          component:()=>import('@/views/content/hello')
        },
        {
          path:'/right',
          name:'hello',
          component:()=>import('@/views/content/right')
        },
        {
          path: '/editshop',
          name: '编辑商品',
          component:()=>import('@/views/content/editshop')
        },
        {
          path: '/cate',
          name: '商品分类',
          component:()=>import('@/views/content/cate')
        },
        {
          path: '/editcate',
          name: '新增商品分类',
          component:()=>import('@/views/content/editcate')
        },
        {
          path: '/supplier',
          name: '商品分类',
          component:()=>import('@/views/content/supplier')
        },
        {
          path: '/editsupplier',
          name: '新增商品分类',
          component:()=>import('@/views/content/editsupplier')
        },
        {
          path: '/users',
          name: '商品分类',
          component:()=>import('@/views/content/users')
        },
        {
          path: '/editusers',
          name: '商品分类',
          component:()=>import('@/views/content/editusers')
        },
        {
          path: '/orders',
          name: '新增商品分类',
          component:()=>import('@/views/content/orders')
        },
        {
          path: '/ordersinfo',
          name: '订单详情',
          component:()=>import('@/views/content/ordersinfo')
        }
      ]
     }
    
   ]
})



